        <section class="x-sticky">
            <h1 class="title" ref="sticky">Sticky 粘滞组件</h1>
            <p class="des">
                用于窗口滚动时固定某元素的组件。
            </p>
            <h2>基础用法</h2>
            <div class="container">
                <div class="example">
                    <x-sticky :distance="60">
                        <div style="height:50px;line-height:50px;text-align:center;background:#36b1bf;font-size:24px;color:#fff;">
                            sticky
                        </div>
                    </x-sticky>
                </div>
                <pre class="code">
<span class="tag">&lt;x-sticky</span> <span class="key">:distance</span><span class="tag">=</span><span class="str">"60"</span><span class="tag">&gt;
        &lt;div&gt;</span>sticky<span class="tag">&lt;/div&gt;</span>
<span class="tag">&lt;/x-sticky&gt;</span></pre>
            </div>
            <h2>Attributes</h2>
            <div class="attr">
                <table>
                    <thead>
                        <tr>
                            <th>参数</th>
                            <th class="des">说明</th>
                            <th class="type">类型</th>
                            <th>可选值</th>
                            <th>默认值</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>distance</td>
                            <td class="des">粘滞位置距离窗口顶部的距离，单位 px，默认100</td>
                            <td class="type">Number</td>
