        <section class="x-menu">
            <h1 class="title" ref="menu">Menu 导航菜单</h1>
            <p class="des">
                导航组件，可无限嵌套子菜单，支持横向和纵向。导航菜单一般配合路由实现页面跳转功能，在<span class="code">selected</span>属性上使用<span class="code">.sync</span>修饰符可实现数据绑定，也可以监听<span class="code">selected-change</span>事件，菜单被点击时将抛出对应条目的<span class="code">name</span>值。
            </p>
