        <section class="x-loading">
            <h1 class="title" ref="loading">Loading 全局加载进度条</h1>
            <p class="des">
                用于展示页面加载、异步请求的加载进度条。全局只会创建一个<span class="code">Loading</span>，因此在任何位置调用的方法都会控制同一个组件。主要在路由切换和<span class="code">AJAX</span>请求中使用，由于这两者都不能拿到精确的进度，<span class="code">Loading</span>会模拟进度。
            </p>
            <h2>基础用法</h2>
            <div class="container">
                <h3>在 Vue 实例中使用</h3>
                <div class="example">
                    <x-button @click="start">Start</x-button>
                    <x-button @click="finish">Finish</x-button>
                    <x-button @click="error">Error</x-button>
                </div>
                <pre class="code">
<span class="tag">&lt;x-button</span> <span class="key">@click</span><span class="tag">=</span><span class="str">"start"</span><span class="tag">&gt;</span>Start<span class="tag">&lt;/x-button&gt;</span>
<span class="tag">&lt;x-button</span> <span class="key">@click</span><span class="tag">=</span><span class="str">"finish"</span><span class="tag">&gt;</span>Finish<span class="tag">&lt;/x-button&gt;</span>
<span class="tag">&lt;x-button</span> <span class="key">@click</span><span class="tag">=</span><span class="str">"error"</span><span class="tag">&gt;</span>Error<span class="tag">&lt;/x-button&gt;</span>
        methods: {
            start() {
                this.$Loading.start();
            },
            finish() {
                this.$Loading.finish();
            },
            error() {
                this.$Loading.error();
            }
        }
</pre>
                <h3>在 Router 中使用</h3>
                <pre class="code">
        <span class="key">import</span> Xue <span class="key">from</span> <span class="str">'xue-ui'</span>
        router.beforeEach((to, from, next) => {
            Xue.Loading.start();
            next();
        })

        router.afterEach((to, from) => {
            Xue.Loading.finish();
        })
</pre>
            </div>
            <h2>Methods</h2>
            <p class="des">
                在 Vue 实例中通过直接调用以下方法来使用组件：
            </p>
            <pre class="code">
this.$Loading.start();
this.$Loading.finish();
this.$Loading.error();</pre>
            <p class="des">
                在 Router 中调用：
            </p>
            <pre class="code">
Xue.Loading.start();
Xue.Loading.finish();
Xue.Loading.error();</pre>
            <div class="attr">
                <table>
                    <thead>
                        <tr>
                            <th>方法名</th>
