                            <td>——</td>
                        </tr>
                        <tr>
                            <td>finish</td>
                            <td class="des">结束进度条，补全剩余进度并自动消失</td>
                            <td>——</td>
                        </tr>
                        <tr>
                            <td>error</td>
                            <td class="des">以错误的类型结束进度条，补全剩余进度并自动消失</td>
                            <td>——</td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <p class="des">
                同时提供了全局配置和全局销毁方法：
            </p>
            <pre class="code">
this.$Loading.config(options);
this.$Loading.destroy();

Xue.Loading.config(options);
Xue.Loading.destroy();</pre>
            <h2>Options</h2>
