<script>
    import mixin from '@/mixin/mixin.js'
    export default {
        name: 'Navigation',
        mixins: [mixin],
        data() {
            return {
                selected1: '',
                selected2: '2-1-1',
                selectedTab: 'social',
                current1: 1,
                current2: 1,
                current3: 1,
                total: 10,
                steps: [
                    { title: '登录', description: '用户登录' },
                    { title: '验证', description: '验证用户身份' },
                    { title: '支付', description: '支付订单' },
                    { title: '完成', description: '订单完成' }
                ],
                current: 0
            }
        },
        methods: {
            onClick1() {
                this.visible1 = !this.visible1;
            },
            onClick2() {
                this.visible2 = !this.visible2;
            },
            start() {
                this.$Loading.start()
            },
            finish() {
                this.$Loading.finish()
            },
            error() {
                this.$Loading.error()
            },
            changeCurrent(n) {
                if (this.current + n > 4 || this.current + n < 0) { return }
                this.current += n
            }
        }
    }
